import React, { Component } from 'react';
import './DiaryPopUp.css';
import SearchFood from './SearchFood.js';
import InputEntry from './InputEntry.js';
import CreateFood from './CreateFood.js';

class DiaryPopUp extends Component {
    constructor(props) {
        super(props);
        this.state = {
            popUpBody: 'search',
            selectedFood: '',
        }
        
        this.newFoodHandler = this.newFoodHandler.bind(this);
        this.searchFoodHandler = this.searchFoodHandler.bind(this);
        this.addEntryHandler = this.addEntryHandler.bind(this);
        this.setView = this.setView.bind(this);
    }
    
    closeHandler(e) {
        this.props.closeHandler(e);
        this.setState({ popUpBody: 'search', selectedFood: '' })
    }
    
    newFoodHandler(e) {
        e.preventDefault();
        this.setState({ popUpBody: 'createFood'})
    }
    
    searchFoodHandler(e, selectedFood) {
        e.preventDefault();
        this.setState({ popUpBody: 'entry', selectedFood: selectedFood });
    }
    
    setView() {
        this.setState({ popUpBody: 'search' });
    }
    
    addEntryHandler(e) {
        this.props.addEntryHandler(e, this.setView);
    }
    
    render() {
        
        let popUpBody = null;
        if (this.state.popUpBody === 'search') {
            popUpBody = <SearchFood newFoodHandler={this.newFoodHandler} searchFoodHandler={this.searchFoodHandler} />
        } else if (this.state.popUpBody === 'entry') {
            popUpBody = <InputEntry selectedFood={this.state.selectedFood} addEntryHandler={this.addEntryHandler} />
        } else if (this.state.popUpBody === 'createFood') {
            popUpBody = <CreateFood />
        }
        
        if (!this.props.isPopUp) {
            return null;
        }
        
        let addEntryError = null;
        if (this.props.addEntryError) {
            addEntryError = (
                <h3 id='errorMessage'>
                    Error adding entry. Please try again.
                </h3>
                );
        }
        
        return (
            <div id='DiaryPopUp'>
                <div id='popUpWrap' style={{marginTop: (this.props.screenPosition + 120) + 'px'}}>
                    <a id='closePopUp' onClick={(e) => this.closeHandler(e)}>
                        <div id='closeIconWrap'>
                            <i className="fa fa-times" aria-hidden="true" />
                        </div>
                    </a>
                    { popUpBody }
                    { addEntryError }
                </div>
            </div>
            );
    }
}

export default DiaryPopUp;


