import React, { Component } from 'react';
import './DiaryBody.css';

class DiaryBody extends Component {
    
    constructor(props) {
        super(props);
        
        this.state = {
            diary: '',
            selected: ''
        }
        
        this.clickHandler = this.clickHandler.bind(this);
        this.deleteHandler = this.deleteHandler.bind(this);
    }
    
    componentWillReceiveProps(nextProps) {
        const newDiary = Object.assign({}, nextProps.diary);
        this.setState({ diary: newDiary });
        };
        
    
    clickHandler(e) {
        e.preventDefault();
        this.setState({ selected: e.target.id });
    }
    
    deleteHandler(e) {
        this.props.deleteHandler(e);
        this.setState({ selected: '' });
    }
    
    render() {
        
        if (!this.props.user) {
            return null;
        }
        
        var foodEntries = null;
        if (this.state.diary.hasOwnProperty('_id')) {
            foodEntries = this.state.diary.food.map(function(entry, index) {
                var selectedEntry = null;
                if (this.state.selected === String(index)) {
                    selectedEntry = (
                        <div id='deleteWrap'>
                            <a onClick={(e) => this.deleteHandler(e)} id={'d' + index}>
                                <i className="fa fa-trash-o" aria-hidden="true" />
                                Delete
                            </a>
                        </div>
                        );
                }
                
              return (
                  <tr className='foodEntry' key={entry._id}>
                    <td className='itemName'>
                        <a onClick={(e) => this.clickHandler(e)} id={index}>
                            {entry.item}
                        </a>
                        <br />
                        <span>
                        {entry.serving.amount + ' ' + entry.serving.unit} 
                        </span>
                    </td>
                    <td className='itemInfo'>
                        <span className='caloriesInfo'>
                            {entry.nutrition.calories.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                        </span>
                        <br />
                        <span className='carbonInfo'>
                            {entry.impact.carbonTotal.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                        </span>
                        <br />
                        <span className='waterInfo'>
                            {entry.impact.waterTotal.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                        </span>
                        { selectedEntry }
                    </td>
                  </tr>
                  ); 
            }, this);
        }
        return (
            <div id='DiaryBody'>
                <table>
                    <tbody>
                        { foodEntries }
                        <tr>
                            <td id='addFoodWrap' colSpan='2'>
                                <a id='addFood' onClick={(e) => this.props.addFoodHandler(e, window.scrollY)}>
                                    <i className="fa fa-plus" aria-hidden="true" />
                                    Add Food
                                </a>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            );
    }
}

export default DiaryBody;


