import React, { Component } from 'react';
import './DiaryHeader.css';
import moment from 'moment';

class DiaryHeader extends Component {
    render() {
        
        const notLoggedIn = () => (
            <div id='notLogged'>
                <h1>
                    Please log in or sign up for a free account
                </h1>
            </div>
            );
        
        const today = moment();
        const yesterday = moment().subtract(1, 'day');
        const tomorrow = moment().add(1, 'day');
        var date = moment(this.props.date);
        
        if (moment(date).isSame(today, 'day')) {
            date = 'Today';
        } else if (moment(date).isSame(yesterday, 'day')) {
            date = 'Yesterday';
        } else if (moment(date).isSame(tomorrow, 'day')) {
            date = 'Tomorrow';
        } else {
            date = moment(this.props.date).format('ddd, MMM DD');
        }
            
        const loggedIn = () => (
            <div id='headerWrap'>
                <div id='date'>
                    <a id='backDay' onClick={(e) => this.props.handler(e, -1)}>
                        <i className='fa fa-arrow-left' aria-hidden='true' />
                    </a>
                    {date}
                    <a id='forwardDay' onClick={(e) => this.props.handler(e, 1)}>
                        <i className='fa fa-arrow-right' aria-hidden='true' />
                    </a>
                </div>
                <div id='infoWrap'>
                    <div id='caloriesWrap' className='buttonDiv'>
                        <h1>
                            { this.props.diary ? this.props.diary.nutrition.caloriesTotal.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : '0' }
                        </h1>
                        <h2>
                            Calories
                        </h2>
                    </div>
                    <div id='carbonWrap' className='buttonDiv'>
                        <h1>
                            { this.props.diary ? this.props.diary.impact.carbonTotal.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : '0' }
                        </h1>
                        <h2>
                            Carbon (Grams)
                        </h2>
                    </div>
                    <div id='waterWrap' className='buttonDiv'>
                        <h1>
                            { this.props.diary ? this.props.diary.impact.waterTotal.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : '0' }
                        </h1>
                        <h2>
                            Water (Liters)
                        </h2>
                    </div>
                </div>
            </div>
            );
            
        let header = null;
        if (this.props.user) {
            header = loggedIn();
        } else {
            header = notLoggedIn();
        }
        
        
        return(
            <div id='DiaryHeader'>
                {header}
            </div>
            );
    }
}

export default DiaryHeader;


