import React, { Component } from 'react';
import './NavBar.css';
import { Link } from 'react-router-dom';

class NavBar extends Component {
    
    render() {
        
        const notLoggedIn = () => (
            <Link to = '/login'>
                <li>
                    Log In
                </li>
            </Link>
            );
            
        const loggedIn = () => (
            <a onClick={this.props.handler}>
                <li>
                    Log Out
                </li>
            </a>
            );
        
        let checkLog = null;
        
        if (this.props.user) {
            checkLog = loggedIn();
        } else {
            checkLog = notLoggedIn();
        }
        
        return (
            <div className='navBar'>
                <div className='logo'>
                    <Link to='/'>
                        myclimatepal
                    </Link>
                </div>
                <div className='buttonsWrap'>
                    <ul>
                        <span id={ this.props.user ? 'profileName' : 'hidden' }>
                            { this.props.user.username }
                        </span>
                        { checkLog }
                        <Link to = { this.props.user ? '/diary' : '/register' }>
                            <li>
                                { this.props.user ? 'Profile' : 'Register' }
                            </li>
                        </Link>
                    </ul>
                </div>
            </div>
            );
    }
}

export default NavBar;


