import React, { Component } from 'react';
import './SearchFood.css';
import $ from 'jquery';

class SearchFood extends Component {
    
    constructor(props) {
        super(props);
        
        this.state = {
            results: ''
        }
        
        this.submitHandler = this.submitHandler.bind(this);
        this.searchFoodHandler = this.searchFoodHandler.bind(this);
    }
    
    submitHandler(e) {
        e.preventDefault();
        const searchTerm = $('#SearchFood #searchQuery').val();
        $.ajax({
            url: '/api/search?search=' + searchTerm,
            success: function(data) {
                if (data.food.length === 0) {
                    return this.setState({ results: 'None' });
                }
                this.setState({ results: data.food });
            }.bind(this),
            error: function() {
                this.setState({ results: 'Error' });
            }.bind(this)
        });
    }
    
    searchFoodHandler(e) {
        let selectedFood = this.state.results[e.target.id];
        this.props.searchFoodHandler(e, selectedFood);
    }
    
    
    render() {
        
        let queryResults = null;
        
        if (this.state.results === 'Error') {
            queryResults = 'An error has occurred. Please try again!';
        } else if (this.state.results === 'None') {
            queryResults = 'No results found';
        } else if (this.state.results.length > 0) {
            queryResults = this.state.results.map(function(result, index) {
                return(
                    <tr className='queryResultRow' key={result._id}>
                        <td className='queryResult'>
                            <a onClick={(e) => this.searchFoodHandler(e)} id={index}>
                                {result._id}
                            </a>
                        </td>
                    </tr>
                    );
            }, this);
        }
        
        return(
            <div id='SearchFood'>
            <form id="search-form_3" onSubmit={(e) => this.submitHandler(e)}>
                <input type="text" className="search_3" id='searchQuery' />
                <a className="submit_3" onClick={(e) => this.submitHandler(e)}>
                    <i className="fa fa-search" aria-hidden="true" />
                </a>
            </form>
            <div id='searchTableWrap'>
                <table>
                    <tbody>
                        { queryResults }
                    </tbody>
                </table>
            </div>
            <div id='createFoodWrap'>
                <a id='createFood' onClick={(e) => this.props.newFoodHandler(e)}>
                    <i className="fa fa-plus" aria-hidden="true" />
                    Create New Food
                </a>
            </div>
            </div>
            );
    }
}

export default SearchFood;


