import React, { Component } from 'react';
import './App.css';
import NavBar from './components/NavBar.js';
import Home from './components/Home.js';
import { Route } from 'react-router-dom';
import Diary from './components/diary/Diary.js';
import Register from './components/Register.js';
import Login from './components/Login.js';
import $ from 'jquery';
import PropTypes from 'prop-types';


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      checkLogin: false,
      user: '',
      invalidAuth: false,
      invalidReg: false,
      registerSuccess: false
    };
    
    // Lift state property of user up so pass handlers to NavBar
    this.registerHandler = this.registerHandler.bind(this);
    this.loginHandler = this.loginHandler.bind(this);
    this.logoutHandler = this.logoutHandler.bind(this);
  }
  
  // check to see if user session is still active on page refresh
  componentWillMount() {
    if (!this.state.checkLogin) {
      $.ajax({
        url: '/api/check',
        dataType: 'json',
        success: function(data) {
          this.setState({ user: data.user });
        }.bind(this),
        complete: function() {
          this.setState({ checkLogin: true });
        }.bind(this)
      })
    }
  }
  
  // change invalidAuth and invalidReg on route change so error message will go away
  componentDidUpdate() {
    if (this.state.invalidAuth || this.state.invalidReg) {
      const unlisten = this.context.router.history.listen(() => {
        this.setState({ invalidAuth: false, invalidReg: false });
        unlisten();
      });
    }
  }
  
  registerHandler(e) {
    e.preventDefault();
    var username = $('#register #username').val();
    var password = $('#register #password').val();
    var payload = {
      username: username,
      password: password
    }
    $.ajax({
      url: '/api/register',
      method: "PUT",
      data: JSON.stringify(payload),
      contentType: "application/json; charset=utf-8",
      dataType: 'json',
      success: function(data) {
        this.setState({ registerSuccess: true, invalidReg: false });
      }.bind(this),
      error: function(err) {
        console.log(err.responseText);
        this.setState({ invalidReg: true });
      }.bind(this)
    });
  }
  
  loginHandler(e) {
    e.preventDefault();
    var username = $('#login #username').val();
    var password = $('#login #password').val();
    $.ajax({
      url: '/api/login?username=' + username + '&password=' + password,
      dataType: 'json',
      success: function(data) {
        this.setState({user: data.user, registerSuccess: false, invalidAuth: false});
        this.context.router.history.push('/diary');
      }.bind(this),
      error: function() {
        this.setState({invalidAuth: true});
      }.bind(this)
    });
  }
  
  logoutHandler(e) {
    e.preventDefault();

    $.ajax({
      url: '/api/logout',
      success: function(data) {
        this.setState({user: ''});
        this.context.router.history.push('/');
      }.bind(this)
    });
  }

  
  
  render() {
    const RegisterProps = () => (
    <Register user= { this.state.user } handler= {this.registerHandler} invalidReg={this.state.invalidReg} registerSuccess={this.state.registerSuccess} />
    );
  
    const LoginProps = () => (
      <Login user= {this.state.user } handler= {this.loginHandler} invalidAuth={this.state.invalidAuth} />
      );
      
    const DiaryProps = () => (
      <Diary user={this.state.user} />
      );
  
    return (
        <div>
          <div>
            <NavBar user= { this.state.user } handler={this.logoutHandler} />
          </div>
          <Route exact path='/' component={Home} />
          <Route path='/register' component={RegisterProps} />
          <Route path='/login' component={LoginProps} />
          <Route path='/diary' component={DiaryProps} />
        </div>
    );
  }
}

App.contextTypes = {
  router: PropTypes.object
};

export default App;



