import React, { Component } from 'react';
import './Login.css';

class Login extends Component {
    
    
    render(){
        let invalidAuth = '';
        if (this.props.invalidAuth) {
            invalidAuth = (
                <p> Invalid username/password
                </p>
                );
        }
        
        const isLoggedIn = () => (
            <div>
                <h1>
                    You are signed in as { this.props.user.username }
                </h1>
            </div>
            );
            
            
        const login = () => (
            <div>
                <h1>
                    Reduce your footprint by making environmentally friendly food choices
                </h1>
                <div id='formWrap'>
                    <form onSubmit={this.props.handler}>
                        <label htmlFor='username'>Username:</label>
                        <input type='text' id='username' />
                        <label htmlFor='password'>Password:</label>
                        <input type='password' id='password' />
                        <input type='submit' value='Log In' />
                    </form>
                </div>
            </div>
            );
        
        let display = null;    
        if (this.props.user) {
            display = isLoggedIn();
            } else {
                display = login();
            }
        return (
            <div id='login'>
            { display }
            { invalidAuth }
            </div>
            );
    }
}

export default Login;


