import React, { Component } from 'react';
import './CreateFood.css';
import $ from 'jquery';

class CreateFood extends Component {
    
    constructor(props) {
        super(props);
        
        this.state = {
            submitSuccess: null
        }
        
        this.submitHandler = this.submitHandler.bind(this);
    }
    
    submitHandler(e) {
        e.preventDefault();
        var foodName = $('#createFormWrap #foodName').val();
        var servingAmount = $('#createFormWrap #servingAmount').val();
        var servingUnit = $("#createFormWrap select[name='servingUnit'] option:selected").val();
        var foodCalories = $('#createFormWrap #foodCalories').val();
        var foodCarbon = $('#createFormWrap #foodCarbon').val();
        var foodWater = $('#createFormWrap #foodWater').val();
        var newFood = {
            _id: foodName,
            serving: {
                amount: servingAmount,
                unit: servingUnit
            },
            nutrition: {
                calories: foodCalories
            },
            impact: {
                carbonPerCal: foodCarbon,
                waterPerCal: foodWater
            }
        };
        $.ajax({
           url: '/api/me/newfood',
           method: "PUT",
           data: JSON.stringify(newFood),
           contentType: "application/json; charset=utf-8",
           dataType: 'json',
           success: function(data) {
               this.setState({ submitSuccess: 'success' });
           }.bind(this),
           error: function(err) {
               console.log(err.responseText);
               this.setState({ submitSuccess: 'error' });
           }.bind(this)
        });
    }
    
    render() {
        
        let submitSuccess = null;
        if (this.state.submitSuccess === 'success') {
            submitSuccess = (
                <h3>
                    New food created.
                    
                </h3>
                );
        } else if (this.state.submitSuccess === 'error') {
            submitSuccess = (
                <h3>
                    Error in creating new food. Please try again.
                </h3>
                );
        }
        
        return (
            <div id='CreateFood'>
                <h1>
                    Create New Food
                </h1>
                <div id='createFormWrap'>
                    <form onSubmit={(e) => this.submitHandler(e)}>
                        <label htmlFor='foodName'>Food Name:</label>
                        <input type='text' id='foodName' />
                        <label htmlFor='servingAmount'>Serving Amount:</label>
                        <input type='text' id='servingAmount' />
                        <label htmlFor='servingUnit'>Serving Unit:</label>
                        <select name="servingUnit">
                            <option value="oz">oz</option>
                            <option value="g">g</option>
                            <option value="cup">cup</option>
                            <option value="mL">mL</option>
                            <option value="serving(s)">serving(s)</option>
                        </select>
                        <br />
                        <label htmlFor='foodCalories'>Calories:</label>
                        <input type='text' id='foodCalories' />
                        <label htmlFor='foodCarbon'>Carbon Footprint (g of CO2 equivalent per calorie):</label>
                        <input type='text' id='foodCarbon' />
                        <label htmlFor='foodWater'>Water Footprint (L per calorie):</label>
                        <input type='text' id='foodWater' />
                        <input type='submit' value='Create' />
                    </form>
                </div>
                { submitSuccess }
            </div>
            );
    }
}

export default CreateFood;


