import React, { Component } from 'react';
import './Register.css';

class Register extends Component {
    render(){
        
        let invalidReg = '';
        if (this.props.invalidReg) {
            invalidReg = (
                <p> Username already taken
                </p>
                );
        }
        
        const isLoggedIn = () => (
            <div>
                <h1>
                    You are signed in as { this.props.user.username }
                </h1>
            </div>
            );
            
        const register = () => (
            <div>
                <h1>
                    Sign up for free to start tracking your water and carbon footprint
                </h1>
                <div id='formWrap'>
                    <form onSubmit={this.props.handler}>
                        <label htmlFor='username'>Username:</label>
                        <input type='text' id='username' />
                        <label htmlFor='password'>Password:</label>
                        <input type='password' id='password' />
                        <input type='submit' value='Register' />
                    </form>
                </div>
            </div>
            );
        
        let display = null;    
        if (this.props.user) {
            display = isLoggedIn();
            } else {
                display = register();
            }
            
        if (this.props.registerSuccess) {
            return (
                <div id='register'>
                    <h1>
                        Registration successful. Please log in.
                    </h1>
                </div>
                );
        }
        
        return (
            <div id='register'>
            { display }
            { invalidReg }
            </div>
            );
    }
}


export default Register;


