import React, { Component } from 'react';
import './InputEntry.css';

class InputEntry extends Component {
    
    
    
    render() {
        let servingUnits = null;
        switch (this.props.selectedFood.serving.unit) {
            case 'g':
            case 'oz':
                servingUnits = (
                    <select name="servingUnit">
                        <option value="oz">oz</option>
                        <option value="g">g</option>
                    </select>
                    );
                break;
            case 'mL':
            case 'cup':
                servingUnits = (
                    <select name="servingUnit">
                        <option value="cup">cup</option>
                        <option value="mL">mL</option>
                    </select>
                    );
                break;
            case 'serving(s)':
                servingUnits = (
                    <select name="servingUnit">
                        <option value="serving(s)">serving(s)</option>
                    </select>
                    );
                break;
            default:
                servingUnits = (
                    <select name="servingUnit">
                        <option value="oz">oz</option>
                        <option value="g">g</option>
                        <option value="cup">cup</option>
                        <option value="mL">mL</option>
                        <option value="serving(s)">serving(s)</option>
                    </select>
                    );
        }
        
        return(
            <div id='InputEntry'>
                <h1>
                    { this.props.selectedFood._id }
                </h1>
                <div id='inputFormWrap'>
                    <form onSubmit={(e) => this.props.addEntryHandler(e)}>
                        <label htmlFor='servingAmount'>Serving Amount:</label>
                        <input type='text' id='servingAmount' />
                        { servingUnits }
                        <div id='logFood'>
                            <input type='submit' value='Log Food' />
                        </div>
                    </form>
                </div>
            </div>
            );
    }
}

export default InputEntry;


