import React, { Component } from 'react';
import './Diary.css';
import $ from 'jquery';
import DiaryHeader from './DiaryHeader.js';
import DiaryBody from './DiaryBody.js';
import DiaryPopUp from './DiaryPopUp.js';
import moment from 'moment';

class Diary extends Component {
    
    constructor(props) {
    super(props);
    
    const todayDate = new Date();
    
    this.state = {
      date: todayDate,
      diary: {},
      isPopUp: false,
      screenPosition: 0,
      addEntryError: false
    };
    
    this.dateHandler = this.dateHandler.bind(this);
    this.addFoodHandler = this.addFoodHandler.bind(this);
    this.closeHandler = this.closeHandler.bind(this);
    this.addEntryHandler = this.addEntryHandler.bind(this);
    this.deleteHandler = this.deleteHandler.bind(this);
    }
    
    // set initial state
    componentDidMount() {
        const date = moment(this.state.date).format('ddd, MMM DD');
        
        if (!this.state.diary.hasOwnProperty(date)) {
            $.ajax({
                url: '/api/me?date=' + this.state.date,
                dataType: 'json',
                success: function(data) {
                    var newDiary = Object.assign({}, this.state.diary);
                    newDiary[date] = data.diary
                    this.setState({ diary: newDiary });
                }.bind(this)
            });
        }
    }
    
    dateHandler(e, change) {
        e.preventDefault();
        let copyDate = new Date(this.state.date.getTime());
        copyDate = copyDate.setDate(copyDate.getDate() + change);
        const newDate = new Date(copyDate);
        this.setState({date: newDate}, function() {
            // only make api call if not in state
            const date = moment(this.state.date).format('ddd, MMM DD');
            if (!this.state.diary.hasOwnProperty(date)) {
                var newDiary = Object.assign({}, this.state.diary);
                $.ajax({
                    url: '/api/me?date=' + this.state.date,
                    dataType: 'json',
                    success: function(data) {
                        newDiary[date] = data.diary
                        this.setState({ diary: newDiary });
                    }.bind(this),
                    error: function() {
                        newDiary[date] = '';
                        this.setState({ diary: newDiary });
                    }.bind(this)
                });
            }
            });
        }
        
    addFoodHandler(e, windowPosition) {
        e.preventDefault();
        this.setState({ isPopUp: true, screenPosition: windowPosition });
    }
    
    closeHandler(e) {
        e.preventDefault();
        this.setState({ isPopUp: false, addEntryError: false });
    }
    
    addEntryHandler(e, cb) {
        e.preventDefault();
        var inputFoodName = $('#InputEntry h1').text();
        var inputServingAmount = $('#InputEntry #servingAmount').val();
        var inputServingUnit = $("#InputEntry select[name='servingUnit'] option:selected").val();
        var newFoodEaten = {
            item: inputFoodName,
            serving: {
                amount: inputServingAmount,
                unit: inputServingUnit
            }
        };
        var payload = {
          foodEaten: newFoodEaten,
          date: new Date()
        };
        
        var newDiary = Object.assign({}, this.state.diary);
        const date = moment(this.state.date).format('ddd, MMM DD');
         $.ajax({
           url: '/api/me/diary',
           method: "PUT",
           data: JSON.stringify(payload),
           contentType: "application/json; charset=utf-8",
           dataType: 'json',
           success: function(data) {
               newDiary[date] = data.diary;
               this.setState({ diary: newDiary, isPopUp: false, addEntryError: false }, cb());
           }.bind(this),
           error: function(err) {
               console.log(err.responseText);
               this.setState({ addEntryError: true });
           }.bind(this)
        });
    }
    
    deleteHandler(e) {
        e.preventDefault();
        const date = moment(this.state.date).format('ddd, MMM DD');
        let index = e.target.id.slice(1);
        let entryID = this.state.diary[date].food[index]._id;
        let payload = { entry: entryID };
        var newDiary = Object.assign({}, this.state.diary);
        $.ajax({
          url: '/api/me/remove',
          method: 'DELETE',
          data: JSON.stringify(payload),
          contentType: "application/json; charset=utf-8",
          dataType: 'json',
          success: function(data) {
              newDiary[date] = data.diary;
              this.setState({ diary: newDiary });
          }.bind(this),
          error: function(err) {
              console.log(err.responseText);
          }
        });
    }
    
    render(){
        const diaryDate = moment(this.state.date).format('ddd, MMM DD');
        
        return (
            <div id='diary'>
                <DiaryHeader date={this.state.date} user={this.props.user} handler={this.dateHandler} diary={this.state.diary[diaryDate]} />
                <DiaryBody diary={this.state.diary[diaryDate]} user={this.props.user} addFoodHandler={this.addFoodHandler} deleteHandler={this.deleteHandler} />
                <DiaryPopUp closeHandler={this.closeHandler} isPopUp={this.state.isPopUp} screenPosition={this.state.screenPosition} addEntryHandler={this.addEntryHandler} 
                addEntryError={this.state.addEntryError} />
            </div>
            );
    }
}

export default Diary;


