import React, { Component } from 'react';
import './Home.css';
import bg from '../pictures/food4.jpg';

class Home extends Component {
    render() {
        return (
            <div id='home'>
                <div className='bgWrap'>
                    <img src={bg} alt='background'>
                    </img>
                    <div id='bgTextWrap'>
                        Track your nutrition's<br></br>environmental impact<br></br>with MyClimatePal
                    </div>
                </div>
                <h1>
                    MyClimatePal is an easy-to-use app that helps you monitor your diet's water and carbon footprint.
                </h1>
                <p>
                    Try for free and see how changing the food you eat can help fight climate change.
                </p>
                <div id='contact'>
                    Created by Hanson Yu<br></br>Email: hansoncyu@gmail.com
                </div>
            </div>
            );
    }
}

export default Home;


